import React, { useEffect, useState } from "react";
import { projectToShow } from "../data";

export default function Projects() {
  const gitHub_url = "https://api.github.com/users/abecus/repos";
  const [projects, setProjects] = useState([]);

  const fetchProjects = async () => {
    const response = await fetch(gitHub_url);
    let data = await response.json();
    data = data.filter((e) => {
      return projectToShow.includes(e.name);
    });
    setProjects(data);
  };

  useEffect(() => {
    fetchProjects();
  }, []);

  return (
    <div className="projects" id="projects">
      {projects.map((repo, index) => {
        const { name, html_url, description } = repo;
        return (
          <article key={index} className="single-project">
            <a href={html_url} className="link title">
              {name}
            </a>
            <p>{description}</p>
          </article>
        );
      })}
    </div>
  );
}
